.StudySession{
  margin-top:1em;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.notesInput{ 
  width: 50%;
}

.rewardForm{
  width:100%;
}

.reward {
  margin-bottom:15%;
}

.rewardInput {
  max-width: 250px;
  width: 100%;
  border:none;
  margin-bottom: 0.5em;
  border-radius: 2px;
  font-size: 1.5rem;
  font-family: 'Montserrat', sans-serif;
}

.timeInput {
  border:none;
  border-radius: 2px;
  font-size: 1.5rem;
  margin-bottom: 0.5em;
  font-family: 'Montserrat', sans-serif;
}

.notesInput {
  border:none;
  width: 80%;
  border-radius: 2px;
  font-size: 1.5rem;
  font-family: 'Montserrat', sans-serif;
}

.studySubmit {
  font-family: 'Montserrat', sans-serif;
  border-radius:3px;
  font-size:1rem;
  height: 2em;
  border:none;
  background-color: #4CAF50;
  cursor:pointer;
  margin-bottom: 1em;
  margin-top: 1em;
}


