  position:relative;
  bottom: 0.4em;
}

::-ms-input-placeholder {
  font-size: 0.5em;
  text-align: center;
  position:relative;
  bottom: 0.4em;
}

::placeholder {
  font-size: 0.5em;
  text-align: center;
  position:relative;
  bottom: 0.4em;
}



